.header {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #ddd;
}

.logo {
  font-family: "IM_Hyemin-Bold";
  line-height: 80px;
}

.today-date {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 24px 0;
}
.today-date p {
  font-size: 18px;
  font-family: "IM_Hyemin-Bold";
  margin-right: 8px;
  padding: 10px 16px;
  background-color: #c2dbf0;
  border-radius: 40px;
}

.today-date b {
  font-size: 18px;
}

.diary-wrap {
  margin: 24px 0;
}

.diary {
  width: 30%;
  float: left;
  height: 420px;
  overflow: hidden;
  box-shadow: 1px 4px 12px -2px #ddd;
  border-radius: 16px;
  margin: 0 32px 32px 0;
}

.diary-top {
  border-bottom: 1px solid #ddd;
  padding: 16px 16px 16px 16px;
}

.diary-title {
  font-size: 18px;
  font-family: "IM_Hyemin-Bold";
  float: left;
  padding-top: 24px;
}

.click-btn {
  float: right;
  display: flex;
}

.click-btn li {
  margin-left: 8px;
  width: 14px;
  height: 14px;
  border-radius: 50px;
}

.delete-btn {
  background-color: #f76660;
}

.alert-btn {
  background-color: #ffc040;
}

.modal-btn {
  background-color: #34ca4c;
}

.diary-date {
  float: left;
  clear: both;
  font-size: 14px;
  margin-top: 8px;
  color: #9e9e9e;
}

.diary-contents {
  padding: 16px;
}

.create-wrap {
  clear: both;
  padding: 80px 0;
  width: 60%;
  margin: auto;
}

.create-title {
  display: block;
  font-family: "IM_Hyemin-Bold";
  text-align: center;
  font-size: 20px;
  padding: 24px 0;
}

.create-wrap p {
  width: 80%;
  margin: 0 auto 12px;
}

.create-wrap p input {
  width: 100%;
  height: 48px;
  border-bottom: 1px solid #ddd;
  padding-left: 16px;
}

textarea {
  width: 100%;
  border-bottom: 1px solid #ddd;
  resize: none;
  height: 200px;
  padding: 16px;
}

textarea:focus {
  outline: none;
}

input[type="submit"] {
  border-radius: 50px;
  background-color: #c2dbf0;
  border-bottom: none;
  padding-left: 0;
}

.updateTxt {
  width: 80%;
  margin: auto;
  font-size: 14px;
  padding-bottom: 24px;
  color: #9e9e9e;
}
