@font-face {
  font-family: "IM_Hyemin-Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/IM_Hyemin-Regular.woff2")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "IM_Hyemin-Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/IM_Hyemin-Bold.woff2")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  transition: all 0.3s;
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  overflow-y: scroll;
}

body {
  font-family: "IM_Hyemin-Regular", sans-serif;
  font-size: 16px;
  font-style: normal;
  color: #2a2a2a;
}

dl,
ul,
ol,
li {
  list-style: none;
}

fieldset,
img,
iframe {
  border: 0 none;
}

img,
video {
  width: 100%;
  max-width: 100%;
  height: 100%;
}

i,
em,
address {
  font-style: normal;
}

a {
  color: #2a2a2a;
  text-decoration: none;
}

span,
a,
address {
  display: block;
  text-decoration: none;
}

legend {
  display: none;
}

input {
  outline: none;
}

ul,
ol {
  list-style: none;
}

.cf:after {
  content: "";
  display: block;
  clear: both;
}

select,
textarea,
input,
button {
  font-family: "IM_Hyemin-Regular", sans-serif;
  font-size: 16px;
}
.container {
  width: 1280px;
  margin: auto;
}
